header {
  width: 100%;
  padding: 1.5%; 
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  
}

#photo-header {
  width: 100%;
  position: relative;
}  

 #self-photo {
  width: 15%;
  position: absolute;
  top: 35%;
  left: 20%;
  padding: 0;
}  

.about-me-text {
  width: 100%;
  position: absolute;
  top: 35%;
  left: 150%;
  padding: 0;
}


.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

}



.regbox {
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: calc(50% - 1em);
  position: relative;
}

.image {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: 0.5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.nameblock {
  transition: 0.5s ease;
  /* Change opacity back to 0 */
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.regbox:hover .image {
  opacity: 0.3;
}

.regbox:hover .nameblock {
  opacity: 1;
}





footer {
  display: flex;
  justify-content: center;
  bottom: 0;
  margin: 1%;
}

.footer-links {
  position: absolute;
  bottom: 0;
  width: 100%;
 }



.fa {
  color:#450E24;
  margin: 1%;
  
}

.fa:hover {
  color: #607A68
}



@media screen and (max-width: 768px) {
  .row {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
  }

  .regbox {
      flex-grow: 0;
      flex-shrink: 1;
      flex-basis: calc(100% - 1em);
  }
}


